"use client";
import React, { useState } from "react";

export default function ContactPage() {
  const [form, setForm] = useState({
    name: "",
    email: "",
    phone: "",
    message: "",
    consent: false,
  });
  const [submitted, setSubmitted] = useState(false);
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value, type } = e.target;
    setForm((prev) => ({
      ...prev,
      [name]: type === "checkbox" ? (e.target as HTMLInputElement).checked : value,
    }));
  };
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setSubmitted(true);
    // Here you would send the form data to your backend or API
  };
  return (
    <section className="w-full bg-[#1A1A1A] min-h-[80vh] py-16 flex flex-col items-center justify-center">
      <div className="max-w-2xl w-full mx-auto px-4 sm:px-6 lg:px-8">
        <h2 className="text-3xl font-semibold text-white text-center mb-2 font-['Bricolage_Grotesque']">Contact Us</h2>
        <p className="text-gray-300 text-center mb-10 text-lg">Get in touch with us for any queries or assistance.</p>
        <form onSubmit={handleSubmit} className="space-y-6 bg-[#181818] p-8 rounded-2xl shadow-lg">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="block text-gray-200 mb-2" htmlFor="name">Full Name</label>
              <input
                type="text"
                id="name"
                name="name"
                value={form.name}
                onChange={handleChange}
                placeholder="Enter your full name"
                className="w-full h-12 px-4 bg-black text-white rounded-lg border border-[#222] font-['Bricolage_Grotesque'] text-base focus:outline-none"
                required
              />
            </div>
            <div>
              <label className="block text-gray-200 mb-2" htmlFor="email">Email Address</label>
              <input
                type="email"
                id="email"
                name="email"
                value={form.email}
                onChange={handleChange}
                placeholder="Enter your email address"
                className="w-full h-12 px-4 bg-black text-white rounded-lg border border-[#222] font-['Bricolage_Grotesque'] text-base focus:outline-none"
                required
              />
            </div>
            <div>
              <label className="block text-gray-200 mb-2" htmlFor="phone">Phone Number</label>
              <input
                type="tel"
                id="phone"
                name="phone"
                value={form.phone}
                onChange={handleChange}
                placeholder="Enter your phone number"
                className="w-full h-12 px-4 bg-black text-white rounded-lg border border-[#222] font-['Bricolage_Grotesque'] text-base focus:outline-none"
                required
              />
            </div>
            <div className="md:col-span-1 col-span-1">
              <label className="block text-gray-200 mb-2" htmlFor="message">Message</label>
              <textarea
                id="message"
                name="message"
                value={form.message}
                onChange={handleChange}
                placeholder="Type your message here"
                className="w-full h-12 px-4 py-2 bg-black text-white rounded-lg border border-[#222] font-['Bricolage_Grotesque'] text-base focus:outline-none min-h-[48px]"
                rows={1}
                required
              />
            </div>
          </div>
          <div className="flex items-center mt-2">
            <input
              type="checkbox"
              id="consent"
              name="consent"
              checked={form.consent}
              onChange={handleChange}
              className="w-5 h-5 mr-2 accent-red-600"
              required
            />
            <label htmlFor="consent" className="text-gray-400 text-sm">
              I agree to receive communications about the real estate universe Group and other projects from the developer.
            </label>
          </div>
          <button
            type="submit"
            className="w-full h-14 bg-[#E50914] text-white rounded-xl text-lg font-semibold mt-4 font-['Bricolage_Grotesque'] hover:bg-[#c40812] transition-colors"
          >
            {submitted ? "Thank you for contacting us!" : "Submit"}
          </button>
        </form>
      </div>
    </section>
  );
} 